import Vue from 'vue';
import Router from 'vue-router';
import Index from '../pages/index';
import Shoedetail from '../pages/shoedetail';

Vue.use(Router);
export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/goodsdetail'
    },
    {
      path: '/goodsdetail',
      component: Index
    },
    {
      path: '/shoedetail',
      component: Shoedetail
    }
  ]
});



