<template>
  <canvas ref="canvas"></canvas>
</template>

<script>
import * as THREE from 'three';
// import Stats from 'three/examples/js/libs/stats.min.js';
import OrbitControls from 'three/examples/js/controls/OrbitControls';
// import Detector from 'three/examples/js/Detector';

import { mapState } from 'vuex';

export default {
  computed: {
    ...mapState(['gltfObj'])
  },
  mounted: function() {
    this._initCanvas();
  },
  methods: {
    _initCanvas: function() {
      let canvas = this.$refs.canvas;
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;

      // //
      // var stats = new Stats();
      // document.body.appendChild(stats.dom);

      /**
       * Scene
       */
      var scene = new THREE.Scene();

      let obj = this.gltfObj;
      obj.scene.children[0].position.set(0, 0, 0);
      scene.add(obj.scene);

      // 
      var ambient = new THREE.AmbientLight(0xffffff);
      ambient.intensity = 1;
      scene.add(ambient);

      /**
       * :
       */
      var camera = new THREE.PerspectiveCamera(
        45,
        window.innerWidth / window.innerHeight,
        0.1,
        10000
      );
      camera.position.set(0, 0, 50);
      camera.lookAt(scene.position);

      /**
       * 
       */
      var renderer = new THREE.WebGLRenderer({
        canvas: canvas,
        alpha: true
      });
      renderer.gammaInput = true; // gamma
      renderer.gammaOutput = true; // gamma
      renderer.setSize(window.innerWidth, window.innerHeight);
      renderer.setClearAlpha(0); //   0-1

      var controls;
      controls = new OrbitControls(camera, renderer.domElement);
      controls.target = scene.position;
      controls.enablePan = false; // 
      controls.enableZoom = true; // 
      controls.autoRotate = false; // 
      controls.minDistance = 20; // 
      controls.maxDistance = 70; // 

      // 
      function render() {
        // stats.update();
        requestAnimationFrame(render); // render
        renderer.render(scene, camera); // 
      }
      render();
      controls.addEventListener('change', render);
    }
  }
};
</script>

<style scoped>
canvas {
  background: url('https://zugou.ninewe.com/test1/model/backone.png') center
    no-repeat cover;
}
</style>



