<template>
  <div class="box">
    <span class="btn sub" @click="onSub">-</span>
    <input class="input" @change="onChange" :value="val" type="number" />
    <span class="btn add" @click="onAdd">+</span>
  </div>
</template>
<script>
export default {
  props: {
    value: {
      type: Number,
      default: 0
    },
    min: {
      type: Number,
      default: 0
    },
    max: {
      type: Number,
      default: 10
    },
    step: {
      type: Number,
      default: 1
    }
  },
  watch: {
    value(newVal, oldVal) {
      this.updateValue(newVal);
    },
    max() {
      this.updateValue(this.val);
    },
    min() {
      this.updateValue(this.val);
    }
  },
  data() {
    return {
      val: 0
    };
  },
  methods: {
    onSub() {
      this.updateValue(this.val - this.step);
    },
    onAdd() {
      this.updateValue(this.val + this.step);
    },
    onChange(e) {
      let newVal = +e.target.value;
      this.updateValue(newVal);
    },
    updateValue(newVal) {
      let val = newVal;
      if (isNaN(val)) {
        val = 0;
      }
      if (val < this.min) {
        val = this.min;
      } else if (val > this.max) {
        val = this.max;
      }
      this.val = val;
      this.$emit('change', val);
    }
  }
};
</script>
<style scoped>
.box {
  width: 154px;
  height: 42px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.btn {
  display: inline-block;
  width: 46px;
  height: 42px;
  text-align: center;
  line-height: 42px;
  font-size: 36px;
}
.btn.sub {
  background-color: #eeeef5;
  border-top-left-radius: 21px;
  border-bottom-left-radius: 21px;
}
.btn.add {
  background-color: #acafb7;
  border-top-right-radius: 21px;
  border-bottom-right-radius: 21px;
}
.input {
  width: 62px;
  height: 100%;
  text-align: center;
  border: none;
  padding: 0;
  background-color: #dbdde2;
  font-size: 28px;
}
</style>



