<template>
  <div class="section">
    <div class="main-img">
      <canvas width="670" height="670" ref="canvas" @click="onCanvasClick"></canvas>
    </div>
    <div class="content">
      <span class="title">{{details.name}}</span>
      <span class="sub-title">{{details.subTitle}}</span>
      <div class="icon-group">
        <span class="price">{{details.price}}</span>
        <img v-show="details.isLike" class="like" src="../assets/like.png" />
        <img v-show="!details.isLike" class="like" src="../assets/like_grey.png" />
        <!-- <button class="share"></button> -->
      </div>
      <span class="instructions">{{details.brandStory}}</span>
      <div>
        <m-instruction title="" :content="details.details"></m-instruction>
        <m-instruction title="" content>
          <img class="size-chart" src="/static/size-chart.png" />
        </m-instruction>
        <m-instruction title="" :content="note"></m-instruction>
        <m-instruction title="" content=" " icon="/static/icon-car.png"></m-instruction>
      </div>
      <div class="about-search">
        <span></span>
        <div class="goods-img-list-box">
          <div class="goods-img-list">
            <img
              @click="goodsTap(index)"
              v-for="(img, index) in goodsImgList"
              :key="index"
              class="search-img"
              :src="img"
            />
          </div>
        </div>
      </div>
    </div>
    <m-row-btn @tap="onShowSpec" v-show="!isShowSpec" position="fixed"></m-row-btn>
    <m-spec-box
      :z-index="11"
      :show="isShowSpec"
      @close="onHideSpec"
      @confirm="onConfirm"
      :goods="details"
    ></m-spec-box>
  </div>
</template>

<script>
import axios from 'axios';
import * as THREE from 'three';

import GLTFLoader from '../utils/GLTFLoader';
// import GLTFLoader from 'three/examples/js/loaders/GLTFLoader';
// import Stats from 'three/examples/js/libs/stats.min.js';

import mRowBtn from '../components/m-row-btn';
import mSpecBox from '../components/m-spec-box';
import mInstruction from '../components/m-instruction';

import wx from 'weixin-js-sdk';

import { mapState, mapMutations } from 'vuex';

export default {
  components: {
    mInstruction,
    mRowBtn,
    mSpecBox
  },
  data: function() {
    return {
      goodsList: [],
      goodsImgList: [],
      details: {},
      isShowSpec: false,
      // 
      note: ` 
      :() 
      
      7(:)()(:)():`
    };
  },
  computed: {
    ...mapState(['gltfObj'])
  },
  created: function() {
    this.goodsNumber = this.$route.query.goodsNumber;
    this._loadGoods();

    axios
      .get('goods/goods_new?user_id=1')
      .then(res => {
        if (res.status === 200) {
          this.goodsList = res.data;
          this.goodsImgList = res.data.map(item => {
            if (item.default_image_url) {
              return `https://www.zugou.vip/media/${item.default_image_url}`;
            } else {
              return '';
            }
          });
        }
      })
      .catch(res => {
        console.log(res);
      });
  },
  mounted: function() {
    this._initCanvas();
  },
  methods: {
    ...mapMutations(['setGltfObj']),
    _initCanvas: function() {
      let canvas = this.$refs.canvas;
      let scene = new THREE.Scene();
      scene.background = new THREE.Color(0xdbdde2);
      // var stats = new Stats();
      // document.body.appendChild(stats.dom);

      let loader = new GLTFLoader();
      let pa = new THREE.Object3D();
      if (!this.gltfObj) {
        loader.load(
          'https://zugou.ninewe.com/test1/model/1.gltf',
          obj => {
            this.setGltfObj(obj);
            // pa.position = new THREE.Vector3(0, 0, 0);
            obj.scene.children[0].position.set(3, 0, -2);
            obj.scene.children[0].parent = pa;
            scene.add(pa);
            scene.add(obj.scene);
          },
          function(xhr) {
            console.log((xhr.loaded / xhr.total) * 100 + '% loaded');
          },
          function(error) {
            console.log(error, 'load error!');
          }
        );
      } else {
        let obj = this.gltfObj;
        obj.scene.children[0].position.set(3, 0, -2);
        obj.scene.children[0].parent = pa;
        scene.add(pa);
        scene.add(obj.scene);
      }

      /**
       * 
       */
      var camera = new THREE.PerspectiveCamera(45, 1, 0.1, 10000);
      camera.position.set(0, 0, 30);
      camera.lookAt(pa.position);
      /**
       * 
       */
      var renderer = new THREE.WebGLRenderer({
        canvas: canvas,
        alpha: true
      });
      renderer.gammaInput = true;
      renderer.gammaOutput = true;
      renderer.setClearAlpha(0);
      // 
      function render() {
        // stats.update();
        requestAnimationFrame(render); // render
        if (pa.rotation.y !== Math.PI * 2) {
          RotationY(pa);
          renderer.render(scene, camera); // 
        }
      }
      render();

      // Y
      function RotationY(obj) {
        pa.rotation.y += 0.05;
        if (pa.rotation.y >= Math.PI * 2) {
          pa.rotation.y = Math.PI * 2;
        }
      }
    },
    _loadGoods() {
      axios
        .get(`/goods/goods_detail?goodsNumber=${this.goodsNumber}&user_id=1`)
        .then(res => {
          console.log(res);
          if (res.status === 200) {
            this.details = res.data;
          }
        })
        .catch(res => {
          console.log(res);
        });
    },
    onCanvasClick: function() {
      this.$router.push({
        path: '/shoedetail'
      });
    },
    onShowSpec() {
      this.isShowSpec = true;
    },
    onHideSpec() {
      this.isShowSpec = false;
    },
    onConfirm(d) {
      let str = encodeURIComponent(JSON.stringify(d));
      wx.miniProgram.navigateTo({
        url: `/pages/order/order?data=${str}`
      });
    },
    goodsTap(index) {
      this.goodsNumber = this.goodsList[index].goodsNumber;
      this._loadGoods();
    }
  }
};
</script>

<style scoped>
.main-img {
  padding: 64px 40px;
}
.content {
  box-shadow: 0px 6px 16px 3px rgba(0, 0, 0, 0.08);
  border-radius: 100px 100px 0px 0px;
  padding: 40px 40px 110px 40px;
  display: flex;
  flex-direction: column;
}
.title {
  font-size: 36px;
  font-weight: 500;
  line-height: 50px;
  text-align: center;
}
.sub-title {
  font-size: 24px;
  font-family: PingFangSC-Light;
  font-weight: 300;
  line-height: 28px;
  margin-top: 4px;
  text-align: center;
}
.icon-group {
  height: 26px;
  width: 100%;
  margin: 8px 0 48px 0;
  position: relative;
  text-align: center;
}
.price {
  font-size: 32px;
  line-height: 45px;
}
.like {
  width: 32px;
  height: 26px;
  position: absolute;
  right: 60px;
  top: 18px;
}
.share {
  width: 25px;
  height: 25px;
  position: absolute;
  right: 4px;
  top: 18px;
  background-repeat: no-repeat;
  background-size: 24px 24px;
  padding: 0;
  border: none;
  background-color: transparent;
  background-image: url('../assets/share.png');
}
.share::after {
  border: none;
}
.instructions {
  font-size: 24px;
  font-weight: 500;
  line-height: 40px;
}
.about-search span {
  display: block;
  width: 100%;
  height: 33px;
  font-size: 24px;
  font-weight: 500;
  line-height: 33px;
  border-top: 1px solid #dbdde2;
  padding: 26px 0;
}
.goods-img-list-box {
  width: 100%;
  height: 160px;
  overflow-y: hidden;
  overflow-x: auto;
  padding: 20px 10px;
}
.goods-img-list {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.search-img {
  height: 100px;
  width: auto;
  margin-right: 20px;
  flex-grow: 0;
  flex-shrink: 0;
}
.size-chart {
  width: 100%;
  height: auto;
  margin: 0 auto;
}
</style>



