<template>
  <div class="container">
    <div class="instructions" @click="onChangeShow">
      <span>{{title}}</span>
      <img class="ins-img" v-show="isShow" src="../assets/sub.png" />
      <img class="ins-img" v-show="!isShow" src="../assets/add.png" />
    </div>
    <div class="content" v-show="isShow">
      <div class="icon-box" v-if="icon">
        <img :src="icon" class="icon" />
      </div>
      <div class="inner-content" v-html="transContent">
      </div>
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    content: {
      type: String,
      default: ''
    },
    icon: {
      type: String,
      default: ''
    }
  },
  computed: {
    transContent() {
      return this.content.replace(/\n/g, '<br/>');
    }
  },
  data: function() {
    return {
      isShow: false
    };
  },
  methods: {
    onChangeShow: function() {
      this.isShow = !this.isShow;
    }
  }
};
</script>

<style scoped>
.container {
  border-top: 1px solid #dbdde2;
  padding: 26px 0;
}
.icon-box {
  display: inline-block;
  width: 24px;
  height: 24px;
  line-height: 24px;
}
.icon {
  max-width: 100%;
  max-height: 100%;
}
.instructions {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.instructions span {
  font-size: 24px;
  font-weight: 500;
}
.ins-img {
  width: 19px;
  height: 19px;
  margin-top: 6px;
}
.content {
  border-top: 1px solid #dbdde2;
  padding: 26px 0;
  font-size: 24px;
  color: #7f8389;
  line-height: 32px;
  font-weight: 300;
  margin-top: 26px;
}
.inner-content {
  display: inline-block;
  min-width: 300px;
}
</style>



