<template>
  <div class="fix-bottom" :style="{ zIndex: zIndex, position: position }">
    <div class="btn" @click="onClick">
      <slot></slot>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    zIndex: {
      type: Number,
      default: 10
    },
    position: {
      type: String,
      value: 'relative'
    }
  },
  methods: {
    onClick() {
      this.$emit('tap');
    }
  }
};
</script>
<style scoped>
.fix-bottom {
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: #000000;
}

.fix-bottom .btn {
  width: 550px;
  height: 68px;
  text-align: center;
  line-height: 68px;
  border-radius: 20px;
  background-color: #f63812;
  font-size: 30px;
  color: #ffffff;
}
</style>



