import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    gltfObj: null
  },
  mutations: {
    setGltfObj(state, obj) {
      state.gltfObj = obj;
    }
  }
});

export default store;



