<template>
  <div>
    <div class="mask" :style="{zIndex: zIndex}" :class="{hidden: !show}" @click="onClose"></div>
    <div class="content" :style="{zIndex: zIndex + 1}" :class="{hidden: !show}">
      <div class="info flex-row">
        <img class="goods-img" :src="pic" />
        <div class="info1 flex-col">
          <span class="price">{{goods.price}}</span>
          <span class="other-info">{{curSpec.size}}</span>
          <span class="other-info">{{curSpec.stock}}</span>
        </div>
      </div>
      <div class="specs">
        <div class="spec-title"></div>
        <div class="spec-list">
          <span
            class="spec-item"
            v-for="(item, index) in specList"
            :key="index"
            :class="{selected: curIndex == index}"
            @click="onSpecSelect(index)"
          >{{item.size}}</span>
        </div>
      </div>
      <div class="flex-row count-info">
        <span class="count-title"></span>
        <m-stepper @change="onCountChange" :max="curSpec.stock"></m-stepper>
      </div>
      <m-row-btn :z-index="zIndex + 2" @tap="onConfirm"></m-row-btn>
    </div>
  </div>
</template>

<script>
import mRowBtn from '../components/m-row-btn';
import mStepper from '../components/m-stepper';
export default {
  components: {
    mRowBtn,
    mStepper
  },
  props: {
    zIndex: {
      type: Number,
      default: 10
    },
    show: {
      type: Boolean,
      default: false
    },
    goods: {
      type: Object,
      default: null
    }
  },
  data: function() {
    return {
      // specList: ['35', '36', '37', '38', '39', '40'],
      curIndex: 0,
      count: 0
    };
  },
  computed: {
    pic() {
      if (this.goods && this.goods.default_image_url) {
        return `https://www.zugou.vip/media/${this.goods.default_image_url}`;
      } else {
        return '';
      }
    },
    curSpec() {
      if (!this.specList[this.curIndex]) {
        return {
          size: '',
          stock: 0
        };
      }
      return this.specList[this.curIndex];
    },
    specList() {
      if (!this.goods || !this.goods.size_stock) {
        return [];
      }
      let arr = this.goods.size_stock.map(item => {
        let obj = {};
        for (let key in item) {
          obj.size = key;
          obj.stock = +item[key];
        }
        return obj;
      });
      return arr;
    }
  },
  methods: {
    onClose() {
      this.$emit('close');
    },
    onSpecSelect(index) {
      this.curIndex = index;
    },
    onCountChange(newVal) {
      this.count = newVal;
    },
    onConfirm() {
      if (this.count === 0) {
        return;
      }
      let curSpec = this.curSpec;
      if (curSpec.stock === 0) {
        return;
      }
      let price = +this.goods.price || 0;
      let totalAmount = price * this.count;
      this.$emit('confirm', {
        size: this.curSpec.size,
        goodsNumber: this.goods.goodsNumber,
        count: this.count,
        total_amount: totalAmount,
        source: 'detail',
        goodsName: this.goods.name,
        price: price,
        goodsPic: this.goods.default_image_url
      });
    }
  }
};
</script>

<style scoped>
.mask {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.4;
  left: 0;
  top: 0;
}
.mask.hidden {
  display: none;
}
.content {
  position: fixed;
  width: 100%;
  background-color: #fff;
  box-shadow: 0px 6px 16px 3px rgba(0, 0, 0, 0.08);
  border-radius: 100px 100px 0px 0px;
  background-color: #ffffff;
  left: 0;
  bottom: 0;
  transition: transform 0.3s linear;
  transform: translateY(0);
}
.content.hidden {
  transform: translateY(100%);
}
.info {
  align-items: flex-end;
  padding: 80px 40px 40px 40px;
}
.info1 {
  padding: 0 20px;
}
.price {
  font-size: 26px;
  color: #000;
}
.other-info {
  font-size: 16px;
  color: #acafb7;
  margin-top: 8px;
}
.goods-img {
  width: 252px;
  height: 208px;
}
.specs {
  padding: 40px;
}
.spec-list {
  border-bottom: 2px solid #d8d8d8;
  padding-bottom: 40px;
}
.spec-title {
  color: #4d4d4d;
  font-size: 30px;
}
.spec-item {
  display: inline-block;
  height: 42px;
  line-height: 42px;
  padding: 0 50px;
  margin: 22px 22px 0 0;
  color: #4d4d4d;
  font-size: 30px;
  background-color: #dbdde2;
  border-radius: 10px;
}
.spec-item.selected {
  background-color: #f63812;
  color: #fff;
}
.count-info {
  width: 670px;
  height: 86px;
  align-items: center;
  justify-content: space-between;
  border-bottom: 2px solid #d8d8d8;
  margin: 0 auto 110px;
}
.count-title {
  font-size: 30px;
  color: #4d4d4d;
}
</style>



